er}div.favorites_wrapper>div.fav_city_container>div.fav_weather_img>img{padding:0 10px;font-size:30px;margin:0 35px}@media (min-width: 767px){div.favorites_wrapper>div.fav_city_container>div.fav_weather_img>img{font-size:50px}}@media (min-width: 992px){div.favorites_wrapper>div.fav_city_container>div.fav_weather_img>img{width:100px}}@media (min-width: 1200px){div.favorites_wrapper>div.fav_city_container>div.fav_weather_img>img{width:120px}}div.favorites_wrapper>div.fav_city_container>div.remove_icon_wrapper{position:absolute;right:0;margin:0 15px;cursor:pointer;display:flex;align-items:center;height:100%}div.favorites_wrapper>div.fav_city_container>div.remove_icon_wrapper>.remove_icon{color:#FF453A;font-size:20px;border-bottom:thin solid #FF453A}@media (min-width: 767px){div.favorites_wrapper>div.fav_city_container>div.remove_icon_wrapper>.remove_icon{font-size:25px}}@media (min-width: 992px){div.favorites_wrapper>div.fav_city_container>div.remove_icon_wrapper>.remove_icon{margin:20px;font-size:35px}}div.favorites_wrapper>div.fav_city_container:hover{color:#30D158}div.favorites_light_mode>div.no_favorites_container>span{color:snow;border:thin solid #FF3B30}div.favorites_light_mode>div.no_favorites_container>div .favorite_icon{color:#FF3B30}div.favorites_light_mode>div.fav_city_container{border:thin solid snow;color:snow}div.favorites_light_mode>div.fav_city_container:hover{color:#fc0}div.favorites_light_mode>div.error_wrapper button{color:snow;border:thin solid snow}

